"use client";
import { useRouter } from "next/navigation";
import { Button, Paper } from "@mui/material";
import { useState } from "react";
import CustomForm from "@/components/CustomForm";
import { USER_FORM_FIELDS } from "@/app/components/user/userFormFields";

const AddUser = () => {
  const router = useRouter();
  const [form, setForm] = useState(
    USER_FORM_FIELDS.reduce((acc, field) => {
      acc[field.key] = field.type === "multiselect" ? [] : "";
      return acc;
    }, {})
  );

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = () => {
    console.log("Form Data:", form);
  };

  return (
    <Paper sx={{ p: 3, maxWidth: 500, margin: "auto" }}>
      <h2>Add User</h2>

      <CustomForm fields={USER_FORM_FIELDS} formData={form} onChange={handleChange} />

      <div style={{ marginTop: "16px", display: "flex", gap: "10px" }}>
        <Button className="btn-primary" onClick={handleSubmit}>
          Save
        </Button>
        <Button className="btn-secondary" onClick={() => router.push("/users")}>
          Back
        </Button>
      </div>
    </Paper>
  );
};

export default AddUser;
